import React, { Fragment } from "react";
import { Col, Row } from "react-bootstrap";
import { Field, reduxForm } from "redux-form";
import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import ThemeButton from "../../../../partials/button/ThemeButton";
import ThemeHeader from "../../../../partials/content/ThemeHeader";
import Item from "../../../../partials/display/Item";
import TextArea from "../../../../partials/form/TextArea";
import TextInput from "../../../../partials/form/TextInput";
import ToggleSwitch from "../../../../partials/form/toggleSwitch/ToggleSwitch";

const validate = combineValidators({
  title: isRequired({ message: "The event title is required" }),
  category: isRequired({ message: "The category is required" }),
  description: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  city: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

const SmsSetup = (props) => {
  const { handleSubmit, pristine, submitting, invalid } = props;

  const handleOnSubmit = (e) => {
    console.log(e);
  };
  return (
    <Fragment>
      <form className="contactForm" onSubmit={handleSubmit(handleOnSubmit)}>
        <Row>
          <Col lg={6}>
            <ThemeHeader
              className="p-3"
              left={<h5 className="title">SMS Setup</h5>}
              right={
                <Fragment>
                  {" "}
                  <p>Credit Balance: $20.83</p>
                </Fragment>
              }
            />
            <Item
              left="Burst SMS key"
              right={
                <Field
                  name="burst_sms_key"
                  component={TextInput}
                  placeholder="Burst SMS API key"
                />
              }
            />
            <Item
              left="Burst SMS Secret"
              right={
                <Field
                  name="burst_sms_secret"
                  component={TextInput}
                  placeholder="Burst SMS API key Secret"
                />
              }
            />
            <Item
              left="Sender Name"
              right={
                <Field
                  name="sender_name"
                  component={TextInput}
                  placeholder="Wiseberry Identifier"
                />
              }
            />

            <Item
              left="Default Signature"
              right={
                <Field
                  name="signature"
                  component={TextArea}
                  row={3}
                  placeholder="Default Signature"
                />
              }
            /><Item
            left="Allow SMS Notification"
            right={
              <Field
                name="allow_notifications"
                component={ToggleSwitch}
              />
            }
          />
          </Col>
          <Col lg={6}>
            <ThemeHeader
              className="p-3"
              left={<h5 className="title">SMS Settings</h5>}
              right={
                <Fragment>
                  {" "}
                  <ThemeButton
                    type="submit"
                    disabled={invalid || pristine || submitting}
                    className="white-outline-button"
                  >
                    Save
                  </ThemeButton>
                </Fragment>
              }
            />
            <div>
                
            </div>
          </Col>
        </Row>
      </form>
    </Fragment>
  );
};

export default reduxForm({
  form: "settingsOfficialDetails",
  initialValues: {},
  validate,
})(SmsSetup);
