import React, { Fragment } from "react";
import { Col, Row } from "react-bootstrap";
import { Field, FieldArray, reduxForm } from "redux-form";
import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import ThemeButton from "../../../../partials/button/ThemeButton";
import ThemeHeader from "../../../../partials/content/ThemeHeader";
import Item from "../../../../partials/display/Item";
import MultiSelect from "../../../../partials/form/MultiSelect";
import NestedSingeInput from "../../../../partials/form/NestedSingeInput";
import SingleAddress from "../../../../partials/form/SingleAddress";
import TextInput from "../../../../partials/form/TextInput";

const validate = combineValidators({
  title: isRequired({ message: "The event title is required" }),
  category: isRequired({ message: "The category is required" }),
  description: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  city: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

const OfficeDetails = (props) => {
  const { handleSubmit, pristine, onHide, submitting, invalid } = props;

  const handleOnSubmit = (e) => {
    console.log(e);
  };
  return (
    <Fragment>
      <form className="contactForm" onSubmit={handleSubmit(handleOnSubmit)}>
        <ThemeHeader
          className="p-3"
          left={<h5 className="title">Office Details</h5>}
          right={
            <Fragment>
              {" "}
              <ThemeButton
                type="submit"
                disabled={invalid || pristine || submitting}
                className="white-outline-button"
              >
                Save
              </ThemeButton>
            </Fragment>
          }
        />
        <Row>
          <Col lg={6}>
            <Item
              left="Agency Name"
              right={
                <Field
                  name="date"
                  showTimeSelect
                  component={TextInput}
                  placeholder="Agency Name"
                />
              }
            />
            <Item
              left="Company Name"
              right={
                <Field
                  name="company_name"
                  showTimeSelect
                  component={TextInput}
                  placeholder="Agency Name"
                />
              }
            />
            <Item
              left="ABN"
              right={
                <Field
                  name="abn"
                  showTimeSelect
                  component={TextInput}
                  placeholder="ABN"
                />
              }
            />
            <Item
              left="Principals"
              right={
                <Field
                  name="principals"
                  showTimeSelect
                  component={TextInput}
                  placeholder="Principals"
                />
              }
            />
            <Item
              left="Licensee in Charge"
              right={
                <Field
                  name="licensee_in_charge"
                  showTimeSelect
                  component={TextInput}
                  placeholder="Licensee in Charge"
                />
              }
            />
            <Item
              left="Licence Number"
              right={
                <Field
                  name="licence_number"
                  showTimeSelect
                  component={TextInput}
                  placeholder="Licence Number"
                />
              }
            />
            <Item
              left="Corporation Number"
              right={
                <Field
                  name="corporation_number"
                  showTimeSelect
                  component={TextInput}
                  placeholder="Corporation Number"
                />
              }
            />
          </Col>
          <Col lg={6}>
            <Item
              left="Address"
              right={
                <Field
                  name="address"
                  showTimeSelect
                  component={SingleAddress}
                />
              }
            />
            <Item
              left="Email Address"
              right={
                <FieldArray
                  name="email"
                  component={NestedSingeInput}
                  label="email"
                  placeholder="Office Email Address"
                  additionalText="Add additional email address"
                />
              }
            />
            <Item
              left="Phone Number"
              right={
                <FieldArray
                  name="phone"
                  component={NestedSingeInput}
                  label="phone"
                  placeholder="Phone Number"
                  additionalText="Add additional phone"
                />
              }
            />
            {/* <Item
            left="Address"
            right={
                <FieldArray
                name='address'
                  component={Address}
                  label="Work Address"
                />
            }
          /> */}
          </Col>
        </Row>
      </form>
    </Fragment>
  );
};

export default reduxForm({
  form: "settingsOfficialDetails",
  initialValues: {
    email: [null],
    phone: [null],
  },
  validate,
})(OfficeDetails);
