import React, { Fragment } from "react";
import { Col, Row } from "react-bootstrap";
import { Field, reduxForm } from "redux-form";
import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import ThemeButton from "../../../../partials/button/ThemeButton";
import ThemeHeader from "../../../../partials/content/ThemeHeader";
import Item from "../../../../partials/display/Item";
import TextInput from "../../../../partials/form/TextInput";

const validate = combineValidators({
  title: isRequired({ message: "The event title is required" }),
  category: isRequired({ message: "The category is required" }),
  description: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  city: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

const DirectUploads = (props) => {
  const { handleSubmit, pristine, submitting, invalid } = props;

  const handleOnSubmit = (e) => {console.log(e)};
  return (
    <Fragment>
      <form
        className="contactForm"
        onSubmit={handleSubmit(handleOnSubmit)}
      >
      <ThemeHeader
        className="p-3"
        left={<h5 className="title">APM Setup</h5>}
        right={
          <Fragment>
            {" "}
            <ThemeButton  type="submit" disabled={invalid || pristine || submitting}  className="white-outline-button">Save</ThemeButton>
          </Fragment>
        }
      />

      <Row>
        <Col lg={6}>
          <Item
            left="Domain"
            right={
              <Field
                name="domain"
                component={TextInput}
                placeholder="Agency ID"
              />
            }
          />
        </Col>
        <Col lg={6}>
          <Item
            left="Portal Name"
            right={
              <Field name="domain_portal_name" placeholder="Agency ID" component={TextInput} />
            }
          />
        </Col>
      </Row>
      </form>
    </Fragment>
  );
};

export default reduxForm({ form: "settingsOfficialDetails",initialValues: {
}, validate })(DirectUploads);

