import React from 'react';
import OfficeDetails from '../pages/officeDetails/OfficeDetails';
import ApmSetup from '../pages/apmSetup/ApmSetup';
import SmsSetup from '../pages/smsSetup/SmsSetup';
import DirectUploads from '../pages/directUploads/DirectUploads';

export default function SettingsPageList({ pageComponent }) {
  console.log(pageComponent);
  switch (pageComponent) {
    case 'Office Details':
      return <OfficeDetails />;
    case 'APM Setup':
      return <ApmSetup />;
    case 'SMS setup':
      return <SmsSetup />;
    case 'Direct Uploads':
      return <DirectUploads />;
    default:
      return ('')
  }

}